import { LoginLink, RegisterLink } from "@kinde-oss/kinde-auth-nextjs/components";
import { Button } from "@/components/ui/button"
import { getKindeServerSession } from "@kinde-oss/kinde-auth-nextjs/server";
import Link from "next/link";
import { ArrowRight, CheckCircle2, Zap, Users, BarChart3 } from "lucide-react";
import { Card, CardContent } from "@/components/ui/card";

export default async function Home() {
  const {isAuthenticated,getUser} = getKindeServerSession()
  const isLoggedIn = await isAuthenticated()
   const user = await getUser(); 

  const features = [
    {
      icon: <Zap className="h-6 w-6 text-primary" />,
      title: "Lightning Fast",
      description: "Streamlined workflow to boost your productivity"
    },
    {
      icon: <Users className="h-6 w-6 text-primary" />,
      title: "Team Collaboration", 
      description: "Work seamlessly with your team members"
    },
    {
      icon: <BarChart3 className="h-6 w-6 text-primary" />,
      title: "Analytics & Insights",
      description: "Track progress with detailed analytics"
    }
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-background via-background to-muted/20">
      {/* Hero Section */}
      <div className="relative overflow-hidden">
        {/* Background Pattern */}
        <div className="absolute inset-0 bg-grid-pattern opacity-5"></div>
        
        <div className="relative w-full min-h-screen flex flex-col items-center justify-center px-4">
          <div className="max-w-5xl mx-auto text-center">
            {/* Badge */}
            <div className="inline-flex items-center gap-2 px-4 py-2 bg-primary/10 text-primary rounded-full text-sm font-medium mb-8 fade-in">
              <CheckCircle2 className="h-4 w-4" />
              Trusted by 10,000+ teams worldwide
            </div>

            {/* Main Heading */}
            <h1 className="text-responsive-xl font-bold tracking-tight mb-6 fade-in-up">
              <span className="block">Your personal workspace</span>
              <span className="block mt-2"> 
                for <span className="gradient-primary bg-clip-text text-transparent">better productivity</span>
              </span>
            </h1>

            {/* Subtitle */}
            <p className="text-responsive-base text-muted-foreground max-w-3xl mx-auto mb-12 fade-in-up stagger-delay-1">
              Transform the way you manage projects with our intuitive platform. 
              Collaborate efficiently, track progress seamlessly, and achieve your goals faster than ever before.
            </p>

            {/* CTA Buttons */}
            <div className="flex flex-col sm:flex-row items-center justify-center gap-4 mb-16 fade-in-up stagger-delay-2">
              {
                isLoggedIn ? (
                  <Button asChild size="lg" className="group shadow-medium hover:shadow-large smooth-transition">
                    <Link href="/workspace" className="flex items-center gap-2">
                      Go to workspace 
                      <ArrowRight className="h-4 w-4 group-hover:translate-x-1 smooth-transition" />
                    </Link>
                  </Button>
                ) : (
                  <>
                    <Button size="lg" className="group shadow-medium hover:shadow-large smooth-transition">
                      <RegisterLink className="flex items-center gap-2">
                        Get Started Free
                        <ArrowRight className="h-4 w-4 group-hover:translate-x-1 smooth-transition" />
                      </RegisterLink>
                    </Button>
                    <Button asChild variant="outline" size="lg" className="shadow-soft hover:shadow-medium smooth-transition">
                      <LoginLink>
                        Sign in
                      </LoginLink>
                    </Button>
                  </>
                )
              }
            </div>

            {/* Features Grid */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 max-w-4xl mx-auto fade-in-up stagger-delay-3">
              {features.map((feature, index) => (
                <Card key={index} className="card-hover border-0 shadow-soft bg-card/50 backdrop-blur-sm">
                  <CardContent className="p-6 text-center">
                    <div className="flex justify-center mb-4">
                      <div className="p-3 bg-primary/10 rounded-full">
                        {feature.icon}
                      </div>
                    </div>
                    <h3 className="text-lg font-semibold mb-2">{feature.title}</h3>
                    <p className="text-sm text-muted-foreground">{feature.description}</p>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Decorative Elements */}
      <div className="absolute top-1/4 left-10 w-20 h-20 bg-primary/10 rounded-full blur-xl animate-pulse"></div>
      <div className="absolute bottom-1/4 right-10 w-32 h-32 bg-purple-500/10 rounded-full blur-xl animate-pulse" style={{ animationDelay: '1s' }}></div>
    </div>
  );
}
